<template>
  <div @load="filterListSec">
    <main class="content">
      <section class="portfolio">
        <div class="container">
          <div class="row justify-content-center align-items-center">
            <h3 class="text-center">Наши работы</h3>
            <nav class="portfolio-navigation d-flex flex-nowrap justify-content-center">
              <ul class="nav-list d-flex">
                <li class="link-1"><input type="radio" value="" id="portfolio-all" name="portfolio-filter" checked  v-model="filterValue"><label for="portfolio-all">Все работы</label></li>
                <li class="link-1"><input type="radio" value="portfolio-logo" id="portfolio-logo" name="portfolio-filter"  v-model="filterValue"><label for="portfolio-logo">Логотипы</label></li>
                <li class="link-1"><input type="radio" value="portfolio-site" id="portfolio-site" name="portfolio-filter"  v-model="filterValue"><label for="portfolio-site">Веб-сайты</label></li>
                <li class="link-1"><input type="radio" value="portfolio-style" id="portfolio-style" name="portfolio-filter"  v-model="filterValue"><label for="portfolio-style">Фирменный стиль</label></li>
                <li class="link-1"><input type="radio" value="portfolio-illustration" id="portfolio-illustration" name="portfolio-filter"  v-model="filterValue"><label for="portfolio-illustration">Иллюстрации</label></li>
                <li class="link-1"><input type="radio" value="portfolio-presentation" id="portfolio-presentation" name="portfolio-filter"  v-model="filterValue"><label for="portfolio-presentation">Презентации</label></li>
                <li class="link-1"><input type="radio" value="portfolio-creative" id="portfolio-creative" name="portfolio-filter"  v-model="filterValue"><label for="portfolio-creative">Креативы</label></li>
              </ul>
            </nav>
          </div>
        </div>
        <div class="portfolio-list row justify-content-center">
          <PagePortfolioItem
              v-for="work in isFilterItems"
              v-bind:work = "work"
          />
        </div>
      </section>
    </main>
    <MailForm />
  </div>
</template>

<script>
import MailForm from '@/components/MailForm'
import PagePortfolioItem from '@/components/PagePortfolioItem'
export default {
  components: { MailForm, PagePortfolioItem },
  data () {
    return {
      isFilterItems: [],
      filterValue: ''
    }
  },
  computed: {
    allPortfolio () {
      return this.$store.getters.allPortfolio
    },
    filterListSec: function () {
      if (this.filterValue === '') {
        this.isFilterItems = this.allPortfolio
      } else {
        this.isFilterItems = this.allPortfolio.filter(item => item.value === this.filterValue)
      }
      return this.isFilterItems
    }
  },
  mounted() {
    this.$store.dispatch('formTopPosition')
  }
}
</script>

<style scoped lang="scss">
.portfolio {
  h3 {
    position: relative;
    padding-bottom: 4rem;
    margin-bottom: 5rem;
    margin-top: 7rem;
    &:after {
      position: absolute;
      bottom: 0;
      left: 50%;
      transform: translateX(-50%);
      width: 42rem;
      height: 1px;
      content: '';
      background-color: #eee;

    }
  }
  &-navigation {
    overflow-x: hidden;
    @media (max-width: 768px) {
      overflow-x: scroll;
    }
    .nav-list {
      margin-bottom: 8rem;
      .link-1 {
        margin-right: 4rem;
        cursor: pointer;
        label {
          cursor: pointer;
          position: relative;
          padding: .5rem;
          transition: .1s linear all;
          &:hover {
            background-color: rgba(0,0,0,0.1);
          }
          &:focus {
            background-color: rgba(0,0,0,0.1);
          }
        }
        input {
          display: none;
          &:checked+label {
            color: #FF5C66;
            top: 1px;
            text-decoration: underline #FF5C66;
          }
        }
        &:last-child {
          margin-right: 0;
        }
      }
      @media (max-width: 998px) {
        .link-1, .link-3 {
          margin-right: 2rem;
          width: 100%;
          display: inline-flex;
          white-space: nowrap;
        }
      }
    }
  }
}
</style>
