        <source v-bind:srcset=work.srcset>
        <img v-bind:src=work.src alt="">
      </picture>
    </router-link>
  </div>
</template>

<script>
export default {
  name: 'PagePortfolioItem',
  props: {
    work: {
      type: Object,
      required: true
    }
  },
  methods: {
    scrollToTop() {
      window.scrollTo(0,0);
    }
  }
}
</script>

<style scoped lang="scss">
.portfolio-list__item {
