  margin: 0;
  padding: 0;
  a {
    overflow: hidden;
    position: relative;
    height: 100%;
    width: 100%;
    display: block;
    transition: .1s linear all;
    &:after {
      position: absolute;
      content: '';
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      background-color: #000;
      opacity: 0;
      transition: .1s linear all;
    }
    &:hover img{
